import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'numberToWords',
})
export class NumberToWordsPipe implements PipeTransform {
  transform(value: any, ...separators: any[]): any {
    if (value == null) {
      return null;
    } else {
      return this.inWords(value /* , separators[0] */);
    }
  }

  private inWords = (n: any /* , separator: any */): any => {
    const a = [
      '',
      'One',
      'Two',
      'Three',
      'Four',
      'Five',
      'Six',
      'Seven',
      'Eight',
      'Nine',
      'Ten',
      'Eleven',
      'Twelve',
      'Thirteen',
      'Fourteen',
      'Fifteen',
      'Sixteen',
      'Seventeen',
      'Eighteen',
      'Nineteen',
    ];

    const b = ['', '', 'Twenty', 'Thirty', 'Forty', 'Fifty', 'Sixty', 'Seventy', 'Eighty', 'Ninety'];

    const g = [
      '',
      'Thousand',
      'Million',
      'Billion',
      'Trillion',
      'Quadrillion',
      'Quintillion',
      'Sextillion',
      'Septillion',
      'Octillion',
      'Nonillion',
    ];

    const arr = (x: any) => {
      return Array.from(x);
    };
    const num = (x: any) => Number(x) || 0;
    const str = (x: any) => String(x);
    const isEmpty = (xs: any) => xs.length === 0;
    const take = (n: any) => (xs: any) => xs.slice(0, n);
    const drop = (n: any) => (xs: any) => xs.slice(n);
    const reverse = (xs: any) => {
      return xs.slice(0).reverse();
    };
    const comp = (f: any) => (g: any) => (x: any) => f(g(x));
    const not = (x: any) => !x;
    const chunk =
      (n: any): any =>
      (xs: any): any => {
        return isEmpty(xs) ? [] : [take(n)(xs), ...chunk(n)(drop(n)(xs))];
      };

    const makeGroup = ([ones, tens, huns]: any) => {
      return [
        num(huns) === 0 ? '' : a[huns] + ' Hundred ',
        num(ones) === 0 ? b[tens] : (b[tens] && b[tens] + '-') || '',
        a[tens + ones] || a[ones],
      ].join('');
    };

    const thousand = (group: any, i: any) => (group === '' ? group : `${group} ${g[i]}`);

    if (typeof n === 'number') return this.inWords(str(n) /* , separator */);
    else if (n === '0') return 'Zero';
    else {
      return comp(chunk(3))(reverse)(arr(n))
        .map(makeGroup)
        .map(thousand)
        .filter(comp(not)(isEmpty))
        .reverse()
        .join(/* separator */ ' and ')
        .trim();
    }
  };
}
