import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'filter',
  pure: false,
})
export class FilterPipe implements PipeTransform {
  transform(value: any[], searchBy: string, searchText: string): any {
    if (value == null) return null;

    const resultArray = [];

    if (!searchText) {
      for (const item of value) resultArray.push(item);
      return resultArray;
    }

    for (const item of value) {
      if (
        (searchBy == 'countryName' || searchBy == 'clientLocationName') &&
        String(item[searchBy]).toLowerCase().startsWith(searchText?.toLowerCase())
      ) {
        resultArray.push(item);
      } else if (
        searchBy == 'dateOfStart' &&
        String(item[searchBy]).toLowerCase().indexOf(searchText?.toLowerCase()) >= 0
      ) {
        resultArray.push(item);
      } else if (
        searchBy != 'countryName' &&
        searchBy != 'clientLocationName' &&
        searchBy != 'dateOfStart' &&
        String(item[searchBy])
          .toLowerCase()
          .split(' ')
          .map((w: string) => w.startsWith(searchText?.toLowerCase()))
          .includes(true)
      ) {
        resultArray.push(item);
      }
    }

    return resultArray;
  }
}
