import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, of, tap } from 'rxjs';
import { Country } from '../models/country';
import { CountriesCacheService } from '../cache-services/countries-cache.service';

const API_BASE_URL = 'https://localhost:7036';

@Injectable({
  providedIn: 'root',
})
export class CountriesService {
  constructor(private httpClient: HttpClient, private cacheService: CountriesCacheService) {}

  getCountries(): Observable<Country[]> {
    const cachedData = this.cacheService.get('cachedData');
    if (cachedData) {
      return of(cachedData);
    } else {
      return this.httpClient.get<Country[]>(`${API_BASE_URL}/api/countries`, { responseType: 'json' }).pipe(
        tap((data) => {
          this.cacheService.set('cachedData', data);
        })
      );
    }
  }

  getCountryByCountryID(countryID: number): Observable<Country> {
    return this.httpClient.get<Country>(`${API_BASE_URL}/api/countries/searchbycountryid/${countryID}`, {
      responseType: 'json',
    });
  }

  insertCountry(newCountry: Country): Observable<Country> {
    return this.httpClient.post<Country>(`${API_BASE_URL}/api/countries`, newCountry, {
      responseType: 'json',
    });
  }

  updateCountry(existingCountry: Country): Observable<Country> {
    return this.httpClient.put<Country>(`${API_BASE_URL}/api/countries`, existingCountry, {
      responseType: 'json',
    });
  }

  deleteCountry(countryID: number): Observable<string> {
    return this.httpClient.delete<string>(`${API_BASE_URL}/api/countries/${countryID}`);
  }
}
