import { Injectable } from '@angular/core';
import { HttpBackend, HttpClient } from '@angular/common/http';
import { Observable, map, of, tap } from 'rxjs';
import { JwtHelperService } from '@auth0/angular-jwt';
import { LoginViewModel } from '../models/login-view-model';
import { SignUpViewModel } from '../models/sign-up-view-model';
import { EmployeesCacheService } from '../cache-services/employees-cache.service';
import { TasksCacheService } from '../cache-services/tasks-cache.service';

const API_BASE_URL = 'https://localhost:7036';

@Injectable({
  providedIn: 'root',
})
export class LoginService {
  private httpClient: HttpClient;

  constructor(
    private httpBackend: HttpBackend,
    private jwtHelperService: JwtHelperService,
    private employeesCacheService: EmployeesCacheService,
    private tasksCacheService: TasksCacheService
  ) {}

  currentUserName: string | any;
  currentUserRole: string | any;

  public Login(loginViewModel: LoginViewModel): Observable<any> {
    this.httpClient = new HttpClient(this.httpBackend);

    return this.httpClient
      .post<any>(`${API_BASE_URL}/authenticate`, loginViewModel, { responseType: 'json', observe: 'response' })
      .pipe(
        map((response) => {
          if (response) {
            this.currentUserName = response.body.userName;
            this.currentUserRole = response.body.role;
            sessionStorage['currentUser'] = JSON.stringify(response.body);
          }
          return response;
        })
      );
  }

  public detectIfAlreadyLoggedIn() {
    if (this.jwtHelperService.isTokenExpired() == false) {
      const currentUser = JSON.parse(sessionStorage['currentUser']);
      this.currentUserName = currentUser.userName;
      this.currentUserRole = currentUser.role;
    }
  }

  public Register(signUpViewModel: SignUpViewModel): Observable<any> {
    this.httpClient = new HttpClient(this.httpBackend);

    return this.httpClient
      .post<any>(`${API_BASE_URL}/register`, signUpViewModel, { responseType: 'json', observe: 'response' })
      .pipe(
        map((response) => {
          if (response) {
            this.employeesCacheService.clear();
            this.currentUserName = response.body.userName;
            this.currentUserRole = response.body.role;
            sessionStorage['currentUser'] = JSON.stringify(response.body);
          }
          return response;
        })
      );
  }

  getUserByEmail(email: string): Observable<boolean> {
    this.httpClient = new HttpClient(this.httpBackend);
    return this.httpClient.get<boolean>(`${API_BASE_URL}/api/getuserbyemail/${email}`, { responseType: 'json' });
  }

  public Logout() {
    this.currentUserName = null;
    this.currentUserRole = null;
    sessionStorage.removeItem('currentUser');
    this.tasksCacheService.clear();
  }

  public isAuthenticated(): boolean {
    if (this.jwtHelperService.isTokenExpired()) {
      return false;
    } else {
      return true;
    }
  }

  public getEmployees(): Observable<any> {
    const cachedData = this.employeesCacheService.get('cachedData');
    if (cachedData) {
      return of(cachedData);
    } else {
      this.httpClient = new HttpClient(this.httpBackend);
      return this.httpClient.get<any>(`${API_BASE_URL}/api/getallemployees`, { responseType: 'json' }).pipe(
        tap((data) => {
          this.employeesCacheService.set('cachedData', data);
        })
      );
    }
  }
}
