import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, of, tap } from 'rxjs';
import { Project } from '../models/project';
import { ProjectsCacheService } from '../cache-services/projects-cache.service';

const API_BASE_URL = 'https://localhost:7036';

@Injectable({
  providedIn: 'root',
})
export class ProjectsService {
  constructor(private httpClient: HttpClient, private cacheService: ProjectsCacheService) {}

  getProjects(): Observable<Project[]> {
    const cachedData = this.cacheService.get('cachedData');
    if (cachedData) {
      return of(cachedData);
    } else {
      return this.httpClient
        .get<Project[]>(`${API_BASE_URL}/api/projects`, {
          responseType: 'json',
        })
        .pipe(
          tap((data) => {
            this.cacheService.set('cachedData', data);
          })
        );
    }
  }

  getProjectByProjectID(projectID: number): Observable<Project> {
    const cachedData = this.cacheService.get('cachedData');
    if (cachedData) {
      return of(cachedData.filter((el: any) => el.projectID == projectID)[0]);
    } else {
      return this.httpClient.get<Project>(`${API_BASE_URL}/api/projects/searchbyprojectid/${projectID}`, {
        responseType: 'json',
      });
    }
  }

  searchProjects(searchBy: string, searchText: string | any): Observable<Project[]> {
    const cachedData = this.cacheService.get('cachedData');
    if (cachedData) {
      let filteredCache = cachedData;
      switch (searchBy) {
        case 'projectID':
          filteredCache = cachedData.filter((el: any) => el.projectID.toString() == searchText);
          break;
        case 'projectName':
          filteredCache = cachedData.filter((el: any) => el.projectName.toLowerCase().includes(searchText));
          break;
        case 'dateOfStart':
          filteredCache = cachedData.filter((el: any) => el.dateOfStart.includes(searchText));
          break;
        case 'teamSize':
          filteredCache = cachedData.filter((el: any) => el.teamSize.toString().includes(searchText));
          break;
        default:
          filteredCache;
      }
      return of(filteredCache);
    } else {
      return this.httpClient.get<Project[]>(`${API_BASE_URL}/api/projects/search/${searchBy}/${searchText}`, {
        responseType: 'json',
      });
    }
  }

  insertProject(newProject: Project): Observable<Project> {
    return this.httpClient.post<Project>(`${API_BASE_URL}/api/projects`, newProject, {
      responseType: 'json',
    });
  }

  updateProject(existingProject: Project): Observable<Project> {
    return this.httpClient.put<Project>(`${API_BASE_URL}/api/projects`, existingProject, {
      responseType: 'json',
    });
  }

  deleteProject(projectID: number): Observable<string> {
    return this.httpClient.delete<string>(`${API_BASE_URL}/api/projects/${projectID}`);
  }
}
