import { Injectable } from '@angular/core';
import { AbstractControl, AsyncValidatorFn, FormControl, ValidationErrors, ValidatorFn } from '@angular/forms';
import { Observable, map } from 'rxjs';
import { LoginService } from './login.service';

@Injectable({
  providedIn: 'root',
})
export class CustomValidatorsService {
  constructor(private loginService: LoginService) {}

  public minimumAgeValidator(minAge: number): ValidatorFn {
    return (control: AbstractControl): ValidationErrors | null => {
      if (!control.value) return null;

      const today = new Date();
      const dateOfBirth = new Date(control.value);
      const diffMilliSeconds = today.getTime() - dateOfBirth.getTime();
      const diffYears = diffMilliSeconds / (1000 * 60 * 60 * 24) / 365.25;

      if (diffYears >= minAge) return null;
      else return { minAge: { valid: false } };
    };
  }

  public compareValidator(controlToValidate: string, controlToCompare: string): ValidatorFn {
    return (control: AbstractControl): ValidationErrors | null => {
      if (!(control.get(controlToValidate) as FormControl).value) return null;

      if ((control.get(controlToValidate) as FormControl).value == (control.get(controlToCompare) as any).value)
        return null;
      else {
        (control.get(controlToValidate) as FormControl).setErrors({ compareValidator: { valid: false } });
        return { compareValidator: { valid: false } };
      }
    };
  }

  public DuplicateEmailValidator(): AsyncValidatorFn {
    return (control: AbstractControl): Observable<ValidationErrors | null> => {
      return this.loginService.getUserByEmail(control.value).pipe(
        map((existingUser: boolean) => {
          if (existingUser) {
            return { uniqueEmail: { valid: false } };
          } else {
            return null;
          }
        })
      );
    };
  }
}
