import { Component, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { Observable, take } from 'rxjs';
import { Country } from '../../models/country';
import { CountriesService } from '../../services/countries.service';
import { CustomValidatorsService } from '../../services/custom-validators.service';
import { LoginService } from '../../services/login.service';
import { SignUpViewModel } from '../../models/sign-up-view-model';

// enum Gender {
//   Male = 'male',
//   Female = 'female',
// }

@Component({
  selector: 'app-sign-up',
  templateUrl: './sign-up.component.html',
  styleUrls: ['./sign-up.component.scss'],
})
export class SignUpComponent implements OnInit {
  signUpForm: FormGroup | any;
  genders = ['male', 'female'];
  // genders: Gender[];
  // genders = Gender;
  countries: Observable<Country[]>;
  registerError: string;

  constructor(
    private countriesService: CountriesService,
    private formBuilder: FormBuilder,
    private customValidatorsService: CustomValidatorsService,
    private loginService: LoginService,
    private router: Router
  ) {}

  ngOnInit() {
    // this.genders = Object.values(Gender);

    this.countries = this.countriesService.getCountries();

    this.signUpForm = this.formBuilder.group(
      {
        personName: this.formBuilder.group(
          {
            firstName: [null, [Validators.required, Validators.minLength(2)]],
            lastName: [null, [Validators.required, Validators.minLength(2)]],
          },
          { updateOn: 'blur' }
        ),
        email: [
          null,
          {
            updateOn: 'blur',
            validators: [Validators.required, Validators.email],
            asyncValidators: [this.customValidatorsService.DuplicateEmailValidator()],
          },
        ],
        mobile: [
          null,
          { updateOn: 'blur', validators: [Validators.required, Validators.pattern(/^(\+\d{1,3}[- ]?)?\d{9,10}$/)] },
        ],
        dateOfBirth: [null, [Validators.required, this.customValidatorsService.minimumAgeValidator(18)]],
        password: [
          null,
          { updateOn: 'blur', validators: [Validators.required, Validators.minLength(5), Validators.maxLength(20)] },
        ],
        confirmPassword: [null, [Validators.required]],
        gender: [null, [Validators.required]],
        countryID: [null, [Validators.required]],
        receiveNewsLetters: false,
        skills: this.formBuilder.array([]),
      },
      {
        validators: [this.customValidatorsService.compareValidator('confirmPassword', 'password')],
      }
    );
  }

  onSubmitClick() {
    this.signUpForm['submitted'] = true;

    if (this.signUpForm.valid) {
      const signUpViewModel = this.signUpForm.value as SignUpViewModel;
      this.loginService
        .Register(signUpViewModel)
        .pipe(take(1))
        .subscribe({
          next: () => {
            console.log('Hello');
            this.router.navigate(['/employee', 'tasks']);
          },
          error: (error) => {
            console.log(error);
            this.registerError = 'Unable to submit';
          },
        });
    }
  }

  onAddSkill() {
    const formGroup = new FormGroup({
      skillName: new FormControl(null, [Validators.required]),
      skillLevel: new FormControl(null, [Validators.required]),
    });

    (<FormArray>this.signUpForm.get('skills')).push(formGroup);
  }

  onRemoveSkill(index: number) {
    (<FormArray>this.signUpForm.get('skills')).removeAt(index);
  }
}
